/* Container for the entire navbar */
.navbar-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    padding: 1rem;
    z-index: 2;
    background-color: var(--foreground);
    box-shadow: var(--box-shadow);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 10px;
    border-bottom: 1px solid var(--border);
  }


  